.background {

    height: 100vh;
    background-color: #333;
}

.headerDiv {
    display: flex;
    margin: auto;
    align-items: center;
}

.titleDiv {

    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5px;
}

.title {
    font-family: monospace;
    font-size: 3em;
    color: #eee;
}

.image {
    border-radius: 50%;
    left: 10px;
    padding: 5px;
    width: 100px;
    height: 100px;
}

.card {
    left: 10px;
    top: 10px;
    justify-content: center;
    align-items: center;
    align-items: center;
    background-color: #222;
    opacity: 0.75;
    display: none;
    position: absolute;
    z-index: 1;
    border-radius: 10px;
    visibility: visible;
}

.cardText {
    color: #eee;
    font-family: monospace;
    font-size: 1.5em;
    padding: 5px;
}

.icon {
    width: 50px;
    height: 50px;
    border-radius: 40%;
    padding: 5px;
    margin-left:  10px;
}